import React from 'react';
import useFetch from '../fetchingData/useFetch';
import { Card } from '@mui/material';
import { CardContent } from '@mui/material';
import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import makeStyles from '@mui/styles/makeStyles';
import Work from "../image/icon-work.svg";
import Play from "../image/icon-play.svg";
import Study from "../image/icon-study.svg";
import Excercise from "../image/icon-exercise.svg";
import Social from "../image/icon-social.svg";
import Selfcare from "../image/icon-self-care.svg";
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import { Link } from 'react-router-dom';

function Daily() {
    const { data } = useFetch()
    const useStyle = makeStyles({
        work: {
            background: `url(${Work})`,
            backgroundRepeat: 'no-repeat',
            backgroundColor: 'hsl(15, 100%, 70%)',
            backgroundPositionX: "right",
            backgroundSize: '5rem',
            backgroundPositionY: "-0.9rem",
            padding: "3rem",
            height: '6rem'
        },
        play: {
            background: `url(${Play})`,
            backgroundRepeat: 'no-repeat',
            backgroundColor: 'hsl(195, 74%, 62%)',
            backgroundPositionX: "right",
            backgroundSize: '5rem',
            backgroundPositionY: "-0.9rem",
            padding: "3rem",
            height: '6rem'
        },
        study: {
            background: `url(${Study})`,
            backgroundRepeat: 'no-repeat',
            backgroundColor: 'hsl(348, 100%, 68%)',
            backgroundPositionX: "right",
            backgroundSize: '5rem',
            backgroundPositionY: "-0.9rem",
            padding: "3rem",
            height: '6rem'
        },
        exercise: {
            background: `url(${Excercise})`,
            backgroundRepeat: 'no-repeat',
            backgroundColor: 'hsl(145, 58%, 55%)',
            backgroundPositionX: "right",
            backgroundSize: '5rem',
            backgroundPositionY: "-0.9rem",
            padding: "3rem",
            height: '6rem'
        },
        social: {
            background: `url(${Social})`,
            backgroundRepeat: 'no-repeat',
            backgroundColor: 'hsl(264, 64%, 52%)',
            backgroundPositionX: "right",
            backgroundSize: '5rem',
            backgroundPositionY: "-0.9rem",
            padding: "3rem",
            height: '6rem'
        },
        selfcare: {
            background: `url(${Selfcare})`,
            backgroundRepeat: 'no-repeat',
            backgroundColor: 'hsl(43, 84%, 65%)',
            backgroundPositionX: "right",
            backgroundSize: '5rem',
            backgroundPositionY: "-0.9rem",
            padding: "3rem",
            height: '6rem'
        }
    })

    const styleHeader = useStyle()


    return (
        <Box sx={{
            marginTop: '6rem'
        }}>
            <h2>{data && data.map(object => {
                const dailyCurrent = object.timeframes.daily.current
                const dailyPrevious = object.timeframes.daily.previous
                const LogicalStyle = () => {
                    if (object.title == 'Work') {
                        return (
                            styleHeader.work
                        )
                    } else if (object.title == "Play") {
                        return (
                            styleHeader.play
                        )
                    } else if (object.title == "Study") {
                        return (
                            styleHeader.study
                        )
                    } else if (object.title == "Exercise") {
                        return (
                            styleHeader.exercise
                        )
                    } else if (object.title == "Social") {
                        return (
                            styleHeader.social
                        )
                    } else {
                        return (
                            styleHeader.selfcare
                        )
                    }
                }
                return (
                    <Box key={object.title} sx={{
                        marginTop: '2rem',
                        marginBottom: '2rem'
                    }}>
                        <Card sx={{
                            borderRadius: '20px',
                            marginLeft: '2rem',
                            marginRight: '2rem',
                        }}>
                            <CardContent className={LogicalStyle()} />
                        </Card>
                        <Card sx={{
                            marginTop: '-4rem',
                            borderRadius: '20px',
                            backgroundColor: 'hsl(235, 46%, 20%)',
                            color: 'white',
                            marginLeft: '1.9rem',
                            marginRight: '1.9rem',
                            transition: '300ms ease-in-out',
                            ['&:hover'] : {
                                backgroundColor: "hsl(235, 45%, 40%)"
                            }
                        }}>
                            <CardContent>
                                <Box sx={{
                                    display: "flex"
                                }}>
                                    <Typography variant="body1" sx={{
                                        flexGrow: '1'
                                    }}>
                                        {object.title}
                                    </Typography>
                                    <Link to="#">
                                        <MoreHorizIcon sx={{
                                            color: "#c2c2c2",
                                            ['&:hover'] : {
                                                color: "white"
                                            }
                                        }} />
                                    </Link>
                                </Box>

                                <Box sx={{
                                    display: "flex",
                                    justifyContent: 'space-between',
                                    alignItems: 'center',
                                    marginTop: '.5rem'
                                }}>
                                    <Typography variant="h5" sx={{
                                        fontWeight: 100,
                                    }}>
                                        {dailyCurrent}hrs
                                    </Typography>

                                    <Typography variant="body2" sx={{
                                        fontWeight: 100,
                                        color: "#c2c2c2"
                                    }}>
                                        Yesterday - {dailyPrevious}hrs
                                    </Typography>
                                </Box>
                            </CardContent>
                        </Card>
                    </Box>
                )
            })}</h2>
        </Box>
    );
}

export default Daily;