import React from 'react';
import Box from '@mui/material/Box';
import { Grid } from '@mui/material';
import { Card, Typography } from '@mui/material';
import Avatar from '@mui/material/Avatar';
import CardHeader from '@mui/material/CardHeader';
import Jeremy from '../image/image-jeremy.png';
import CardContent from '@mui/material/CardContent';
import { Link, useLocation } from "react-router-dom";
import { makeStyles } from '@mui/styles';

function Layout( {children} ) {

    const location = useLocation()

    const useStyle = makeStyles(theme => ({
        Link: {
            textDecoration: 'none',
            color: '#B0B0B0',
            fontFamily: 'Rubik',
        },
        margin:{
            marginLeft: '10rem',
            marginRight: "10rem"
        },
        LinkActive: {
            textDecoration: 'none',
            color: 'white',
            fontFamily: 'Rubik',
        },
        "@media (max-width: 800px)" : {
            margin: {
                marginLeft: '0rem',
                marginRight: "0rem"
            }
        }
     })
    )
    const mainStyling = {
        card: {
            mainBackground: 'hsl(246, 80%, 60%)'
        }
    }

    const style = useStyle()
    
    return (
        <Box className={style.margin}>
            <Card sx={{
                backgroundColor: 'hsl(235, 46%, 20%)',
                marginTop: '7rem',
                marginBottom: '-12rem',
                marginLeft: '2rem',
                marginRight: '2rem',
                borderRadius: '20px'
            }}>
                <CardContent>
                    <Box sx={{
                        marginTop:'5rem',
                        display: 'flex',
                        justifyContent: 'space-evenly',
                    }}>
                        <Link to="daily" className={location.pathname === "/daily" ? style.LinkActive : style.Link}>Daily</Link>
                        <Link to="weekly" className={location.pathname === "/weekly" ? style.LinkActive : style.Link}>Weekly</Link>
                        <Link to="monthly" className={location.pathname === "/monthly" ? style.LinkActive : style.Link}>Monthly</Link>
                    </Box>
                </CardContent>
            </Card>
            <Card sx={{
                backgroundColor: mainStyling.card.mainBackground,
                margin: '0rem 2rem',
                padding: '1rem',
                borderRadius: '20px'
            }}>
                <CardHeader 
                avatar={
                    <Avatar sx={{
                        backgroundColor: 'white',
                        width: '4rem',
                        height: '4rem'
                    }}>
                        <img src={Jeremy} alt="" id='jeremy'/>
                    </Avatar>
                }
                title={
                    <Box>
                        <Typography variant="body2" sx={{
                            fontFamily: 'Rubik',
                            color: '#C2C2C2'
                        }}>Report For</Typography>
                        <Typography variant="h6" component="h6" color="white" sx={{
                            fontFamily: 'Rubik',
                            fontWeight: 100,
                            fontSize: '21px'
                        }}>Jeremy Robson</Typography>
                    </Box>
                }
                />
            </Card>

            {children}
        </Box>
    );
}

export default Layout;